#footer {
    margin-left: 375px;
    text-align: center;
    background-color: #dce3e2;
    padding: 3em 0 4em 0;
    box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.05), inset 0 0.1em 0.1em 0 rgba(0, 0, 0, 0.025);
    font-size: 0.8em;
}

    #footer .copyright {
        cursor: default;
        margin: 0;
    }

        #footer .copyright li {
            display: inline-block;
            line-height: 1em;
            border-left: solid 1px rgba(128, 128, 128, 0.35);
            padding: 0 0 0 0.5em;
            margin: 0 0 0 0.5em;
        }

            #footer .copyright li:first-child {
                border-left: 0;
                padding-left: 0;
                margin-left: 0;
            }
