    /* List */
    
        ul {
            list-style: disc;
            padding-left: 1em;
        }
    
            ul li {
                padding-left: 0.5em;
            }
    
        ol {
            list-style: decimal;
            padding-left: 1.25em;
        }
    
            ol li {
                padding-left: 0.25em;
            }
    
    /* Icons */
    
        ul.icons {
            cursor: default;
            list-style: none;
            padding-left: 0;
        }
    
            ul.icons li {
                display: block;
                padding-left: 0;
            }
    
            ul.icons a {
                display: inline-block;
                width: 2em;
                height: 2em;
                line-height: 2em;
                text-align: center;
                border: 0;
            }
    
