    }
        form label {
            display: block;
            text-align: left;
            margin-bottom: 0.5em;
        }
    
        form input[type="text"],
        form input[type="email"],
        form input[type="password"],
        form select,
        form textarea {
            position: relative;
            -webkit-appearance: none;
            display: block;
            border: 0;
            outline: 0;
            background: #fff;
            background: rgba(255, 255, 255, 0.75);
            width: 100%;
            border-radius: 0.35em;
            padding: 0.75em 1em 0.75em 1em;
            box-shadow: inset 0 0.1em 0.1em 0 rgba(0, 0, 0, 0.05);
            border: solid 1px rgba(0, 0, 0, 0.15);
            -moz-transition: all 0.35s ease-in-out;
            -webkit-transition: all 0.35s ease-in-out;
            -ms-transition: all 0.35s ease-in-out;
            transition: all 0.35s ease-in-out;
        }

        .button {
            position: relative;
            display: inline-block;
            border-radius: 0.35em;
            color: #fff !important;
            text-decoration: none;
            padding: 0.75em 2.5em 0.75em 2.5em;
            background-color: #8ebebc;
            border: 0;
            cursor: pointer;
            background-image: -moz-linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.15)), url("./overlay.png");
            background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.15)), url("./overlay.png");
            background-image: -ms-linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.15)), url("./overlay.png");
            background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.15)), url("./overlay.png");
            -moz-transition: background-color 0.35s ease-in-out;
            -webkit-transition: background-color 0.35s ease-in-out;
            -ms-transition: background-color 0.35s ease-in-out;
            transition: background-color 0.35s ease-in-out;
        }
    
